<template>
  <div>
    <br>
    <b-col offset="2">
      <span> <h4>
        Create Bank transfer file
        </h4>
        </span>
    </b-col>
    <br>
     <b-row class="mt-1">
      <b-col cols="1" class="col-left" offset="2">
        <label for="input-name">File name:</label>
      </b-col>
      <b-col cols="2">
        <b-form-input
          class="form-control form-control-sm"
          id="input-name"
          v-model="filename"
          placeholder="Enter a name for the file"
        ></b-form-input>
      </b-col>
    </b-row>
    <b-row>
    <b-col cols="2" offset="2">
      <br>
    <b-button variant="dark" class="mr-1" @click.prevent="getBankTransferData()"
        >Get Data</b-button
      >
       <b-button variant="dark" class="mr-1" @click.prevent="createFile()"
        >Create CSV file</b-button
      >
    </b-col>
    </b-row>
    <br>
     <b-row class="mt-1">
      <b-col cols="1" class="col-left" offset="2">
        <label for="input-name"> Total Records:</label>
      </b-col>
      <b-col cols="2">
        {{this.totalRows}}
      </b-col>
    </b-row>
       <div v-show="isBusy">
      <template>
        <div class="text-center text-danger my-2">
          <b-spinner class="align-middle"></b-spinner>
          <h4><strong>Please wait...</strong></h4>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      isBusy: false,
      bankTransferData: [],
      totalRows: 0,
      csv: [],
      filename: ''
    }
  },
  methods: {
    toggleBusy () {
      this.isBusy = !this.isBusy
    },
    convertToCSV (arr) {
      const array = [Object.keys(arr[0])].concat(arr)

      return array.map(it => {
        return Object.values(it).toString()
      }).join('\n')
    },
    async getBankTransferData () {
      const url = process.env.VUE_APP_API_URL + '/bankTransfer'
      fetch(url)
        .then((res) => res.json())
        .then((data) => {
          console.log('Output: ', data)
          this.bankTransferData = data
          this.totalRows = this.bankTransferData.length
        })
        .catch((err) => console.error(err))
    },
    createFile () {
      this.toggleBusy()
      this.csv = this.convertToCSV(this.bankTransferData)
      // console.log(
      //   this.convertToCSV(this.bankTransferData)
      // )
      this.saveToFile()
      this.toggleBusy()
    },
    saveToFile () {
      // alert('saving file')
      this.downloadBlob(this.csv, `${this.filename}.csv`, 'text/csv;charset=utf-8;')
    },
    downloadBlob (content, filename, contentType) {
      // Create a blob
      const blob = new Blob([content], { type: contentType })
      const url = URL.createObjectURL(blob)

      // Create a link to download it
      const pom = document.createElement('a')
      pom.href = url
      pom.setAttribute('download', filename)
      pom.click()
    }

  }

}

</script>

